import React from 'react'
import { NavLink } from 'react-router-dom'
import FullInfoCardContainer from '../components/FullInfoCard/FullInfoCardContainer'
import styles from './InfoPage.module.css'

const InfoPage = () => {
    return (
        <div className={styles.wrapper}>
            <FullInfoCardContainer />
            <NavLink className={styles.link} to='/'><img src={require('../assets/left-arrow.png')} /></NavLink>
        </div>
    )
}

export default InfoPage