const API_KEY = '86dc079dec9ed2caa1f4b824666f529d'

const api = {
    getWeather: async (city) => {
        const apiCall = await fetch(`https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=${API_KEY}`)
        const res = await apiCall.json()
        return res
    }
}

export default api