import React from 'react'
import styles from './Search.module.css'

const Search = (props) => {

    const onInputChange = (e) => {
        props.updateSearchValue(e.target.value)
    }

    const onAddCity = () => {
        props.addCity()
    } 

    return (
        <div className={styles.searchForm}>
            <input onChange={onInputChange} value={props.searchValue} placeholder='Enter city' type='text' />
            <button onClick={onAddCity} >ADD CITY</button>
        </div>
    )
}

export default Search
