import React from 'react'
import Cards from './Cards'
import { removeCityAC, changeCityForFullInfoAC } from '../../redux/appReducer'
import { connect } from 'react-redux'

const mapStateToProps = (state) => {
    return {
        cities: state.appReducer.cities
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        removeCity: (city) => {
            dispatch(removeCityAC(city))
        },
        changeCity: (city) => {
            dispatch(changeCityForFullInfoAC(city))
        }
    }
}

const CardsContainer = connect(mapStateToProps, mapDispatchToProps)(Cards)

export default CardsContainer