import React, { useEffect, useState } from 'react'
import { NavLink } from 'react-router-dom'
import { getWeatherIcon, convertTemp } from '../../utils/utils'
import api from '../../api/api'
import styles from './Card.module.css'
import Preloader from '../common/Preloader'


const Card = ({city, removeCity, changeCity}) => {    
    const [data, setData] = useState({})

    useEffect(() => {
        getWeatherData()
    }, [])

    const getWeatherData = async () => {
        const res = await api.getWeather(city)
        setData({
            city: res.name,
            icon: await require(`../../assets/${getWeatherIcon(res.weather[0].id)}`),
            celsius: convertTemp(res.main.temp),
            description: res.weather[0].description,
        })
    }

    if (!data.city) {
        return <Preloader />
    }
    
    return (
        <>
            <div className={styles.actions}>
                <img className={styles.refreshButton} onClick={() => {
                    setData({})
                    getWeatherData()
                }} src={require('../../assets/refresh.png')} alt='refresh'/>

                <img className={styles.removeButton} onClick={() =>{
                    removeCity(city)
                }} src={require('../../assets/remove.png')} alt='remove'/>
            </div>
            <NavLink onClick={() => changeCity(city)} to='moreInfo' className={styles.navLink}>
                <div className={styles.card}> 
                    <h3>{data.city}</h3>
                    <div className={styles.weatherDataBlock}>
                        <img src={data.icon} alt='weather-icon'/>
                        <span>{data.celsius}&deg;</span>
                    </div>
                </div>
            </NavLink>
        </>
    )
}

export default Card