import React, { useState, useEffect } from 'react'
import api from '../../api/api'
import { getWeatherIcon, convertTemp } from '../../utils/utils'
import Preloader from '../common/Preloader'
import styles from './FullInfoCard.module.css'


const FullInfoCard = ({city}) => {
    const [data, setData] = useState({})

    useEffect(() => {
        getWeatherData()
    }, [])

    const getWeatherData = async () => {
        const res = await api.getWeather(city)
        console.log(res)
        setData({
            city: res.name,
            country: res.sys.country,
            icon: await require(`../../assets/${getWeatherIcon(res.weather[0].id)}`),
            celsius: convertTemp(res.main.temp),
            tempMin: convertTemp(res.main.temp_min),
            tempMax: convertTemp(res.main.temp_max),
            description: res.weather[0].description
        })
    }
    
    if (!data.city) {
        return <Preloader />
    }

    return (
        <div className={styles.fullCard}>
            <h3>{data.city} {data.country}</h3>
            <hr/>
            <img src={data.icon} className={styles.icon} />
            <span>Weather: {data.description}</span>
            <span className={styles.temperature}>Temperature: {data.celsius}&deg;</span>
            <div className={styles.minMaxBlock}>
                <span >Min: {data.tempMin}&deg;</span>
                <span >Max: {data.tempMax}&deg;</span>
            </div>
        </div>
    )
}

export default FullInfoCard