import { removeItemFromArray } from '../utils/utils'

const ADD_CITY = 'app/ADD-CITY'
const REMOVE_CITY = 'app/REMOVE-CITY'
const CHANGE_CITY_FOR_FULL_INFO = 'app/CHANGE-CITY-FOR-FULL-INFO'
const UPDATE_SEARCH_VALUE = 'app/UPDATE-SEARCH-VALUE'

let cities

if (localStorage['cities']) {
    cities = localStorage['cities'].split(',')
} else {
    cities = ['Kharkiv']
}


let initialState = {
    cities: cities,
    searchValue: '',
    cityForFullInfo: ''
}

const appReducers = (state = initialState, action) => {
    switch (action.type) {
        case ADD_CITY:
            localStorage['cities'] = [...state.cities, state.searchValue] 
            return {
                ...state,
                cities: [...state.cities, state.searchValue],
                searchValue: ''
            }
            
        case REMOVE_CITY:
            return {
                ...state,
                cities: [...removeItemFromArray(action.city, state.cities)]
            }
        
        case CHANGE_CITY_FOR_FULL_INFO:
            return {
                ...state,
                cityForFullInfo: action.city
            }
        
        case UPDATE_SEARCH_VALUE:
            return {
                ...state,
                searchValue: action.newValue
            }
        

        default:
            return state
    }
}

export const addCityAC = () => ({type: ADD_CITY})

export const removeCityAC = (city) => ({
    type: REMOVE_CITY,
    city
})

export const changeCityForFullInfoAC = (city) => ({
    type: CHANGE_CITY_FOR_FULL_INFO,
    city
})

export const updateSearchValueAC = (text) => ({
    type: UPDATE_SEARCH_VALUE,
    newValue: text
})


export default appReducers