export const removeItemFromArray = (item, array) => {
    let newArray = [...array]
    const index = newArray.indexOf(item)

    if (index > -1) {
        newArray.splice(index, 1)
    }

    return newArray
}

export const getWeatherIcon = (range) => {
    switch (true) {
      case range >= 200 && range < 232:
        return 'thunderstorm.png'
      case range >= 300 && range <= 321:
        return 'sleet.png'
      case range >= 500 && range <= 521:
        return 'rain.png'
      case range >= 600 && range <= 622:
        return 'snow.png'
      case range >= 701 && range <= 781:
        return 'fog.png'
      case range === 800:
        return 'sun.png'
      case range >= 801 && range <= 804:
        return 'cloud.png'
      default:
        return 'moon.png'
    }
}

export const convertTemp = (temp) => {
  return Math.floor(temp - 273.15)
}

